#benefits-block {
    background-image: url('./img/benefits-bg.png');
    .benefits {
        &__container {
        }

        &__title {
            text-align: center;
            color: var(--card);
            margin-bottom: 10px;
            &::after {
                left: 50%;
                top: 0;
                transform: translate(-50%, -50%);
            }
        }

        &__descr {
            color: var(--card);
            margin-bottom: 40px;
            text-align: center;
        }

        &__list {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 30px;
        }

        &__img {
            background: var(--accent);
            width: 70px;
            height: 70px;
            border-radius: 5px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 20px;
            svg, img {
                width: 50px;
                height: 50px;
            }
        }
        &__item {
            padding: 30px;
            border-radius: 5px;
            background: var(--card);
        }
        &__name {
            color: var(--head);
            text-align: start;
            margin-bottom: 10px;
        }
        &__text {
            color: var(--text);
        }
    }
    .container {
    }
    .h1 {
    }
    .section {
        &__title {
        }
    }
    .h2 {
    }
}


@media screen and (max-width: 867px) {
#benefits-block .benefits__list {
    grid-template-columns: repeat(2, 1fr);
}
}


@media screen and (max-width: 650px) {
#benefits-block .benefits__list {
    grid-template-columns: 1fr;
}
}