#services-block {
    .services {
        &__container {
        }

        &__title {
            margin-bottom: 60px;
            text-align: center;
            
            &::after {
                left: 50%;
                top: 0;
                transform: translate(-50%, -50%);
            }
        }
        &__btn {
            margin: 0px auto;
        }

        &__list {
            display: grid;
            grid-template-columns: repeat(12, 1fr);
            grid-template-rows: repeat(2, 1fr);
            grid-column-gap: 0px;
            grid-row-gap: 0px;
            margin-bottom: 60px;
        }

        &__item {
            position: relative;
            padding: 30px;
            padding-bottom: 54px;
            &:hover .services__bg {
                opacity: 1;
            }
        }

        &__img {
            max-width: 130px;
            max-height: 130px;
            margin: 0px auto;
            margin-bottom: 20px;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &__name {
            max-width: 330px;
            text-align: center;
            margin-bottom: 10px;
        }

        &__descr {
            max-width: 330px;
            text-align: center;
        }

        &__bg {
            position: absolute;
            opacity: 0;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
            height: 100%;
            border-radius: 5px;
            transition: 0.3s ease-in-out opacity;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

        }
    }
    .services__item:nth-child(1) {
        grid-area: 1 / 1 / 2 / 5;
    }
    .services__item:nth-child(2) {
        grid-area: 1 / 5 / 2 / 9;
    }
    .services__item:nth-child(3) {
        grid-area: 1 / 9 / 2 / 13;
    }
    .services__item:nth-child(4) {
        grid-area: 2 / 1 / 3 / 4;
    }
    .services__item:nth-child(5) {
        grid-area: 2 / 4 / 3 / 7;
    }
    .services__item:nth-child(6) {
        grid-area: 2 / 7 / 3 / 10;
    }
    .services__item:nth-child(7) {
        grid-area: 2 / 10 / 3 / 13;
    }
}


@media screen and (max-width: 884px) {
    #services-block .services__list {
        grid-template-columns: repeat(2, 1fr);
    }
    #services-block .services__item {
        grid-area: unset !important;
    }
}

@media screen and (max-width: 520px) {
    #services-block .services__list {
        grid-template-columns: 1fr;
    }
}