.steps {
    &__container {
    }

    &__title {
        text-align: center;
        margin-bottom: 10px;
        &::after {
            left: 50%;
            top: 0;
            transform: translate(-50%, -50%);
        }
    }

    &__descr {
        margin-bottom: 60px;
        color: var(--text);
        text-align: center;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-column-gap: 0px;
        grid-row-gap: 0px;
        margin: 0px auto;
        margin-bottom: 56px;
        position: relative;
        &::after {
            content: '';
            width: 1920px;
            height: 2px;
            background: var(--stroke);
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    &__item {
        position: relative;
        min-height: 156px;
        padding-left: 17px;
        border-left: 2px solid var(--stroke);
        display: flex;
        flex-direction: column;
        &::after {
            position: absolute;
            left: -2px;
            top: 0;
            content: '';
            width: 2px;
            height: 82px;
            background: var(--accent);
        }
        &::before {
            content: '';
            position: absolute;
            background: #000;
            width: 20px;
            height: 22px;
            background: url(./img/polygon-svg.svg);
            left: -11px;
            bottom: -10px;
            z-index: 2;
        }
        &:nth-child(1) {
            grid-area: 1 / 1 / 2 / 3;
        }
        &:nth-child(2) {
            grid-area: 2 / 2 / 3 / 4;
            justify-content: flex-end;
            &::after {
                top: unset;
                bottom: 0;
            }
            &::before {
                bottom: unset;
                top: -10px;
            }
        }
        &:nth-child(3) {
            grid-area: 1 / 3 / 2 / 5;
            min-height: 134px;
        }
        &:nth-child(4) {
            justify-content: flex-end;
            grid-area: 2 / 4 / 3 / 6;
            min-height: 161px;
            &::after {
                top: unset;
                bottom: 0;
            }
            &::before {
                bottom: unset;
                top: -10px;
            }
        }
        &:nth-child(5) {
            grid-area: 1 / 5 / 2 / 7;
            min-height: 161px;
        }
    }

    .parent {
        
    }


    &__name {
        margin-bottom: 10px;
    }

    &__text {
        max-width: 214px;
        color: var(--text);
    }

    &__btn {
        margin: 0px auto;
    }
}

.parent {
}


@media screen and (max-width: 992px) {
.steps__list {
    grid-template-columns: 1fr;
    justify-content: center;
    max-width: fit-content;
}
.steps__item {
    grid-area: unset !important;
    justify-content: flex-start !important;
    min-height: 100px !important;
    max-width: fit-content;
}
.steps__item:nth-child(2)::before,
.steps__item:nth-child(4)::before {
    bottom: -10px;
    top: unset;
}
.steps__item::after {
    content: unset;
}
.steps__list::after {
    content: none;
}

}
