*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  position: relative;
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
}

h1,
.h1 {
  margin: 0;
  font-family: Montserrat;
  font-size: 40px;
  font-weight: 700;
  line-height: 48.76px;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
}

h2,
.h2 {
  margin: 0;
  //styleName: H2;
  font-family: Montserrat;
  font-size: 20px;
  font-weight: 600;
  line-height: 24.38px;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
}
.h3,
h3 {
  margin: 0;
  //styleName: H3;
  font-family: Montserrat;
  font-size: 18px;
  font-weight: 400;
  line-height: 24.3px;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
}

a {
  text-decoration: none;
  color: var(--head);
  transition: 0.3s all ease-in-out;
  &:hover {
    color: var(--hover);
  }
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

input {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
  outline: none;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
  &:focus {
    border: 2px solid var(--hover);
  }
}

textarea {
  outline: none;
  &:focus {
    border: 2px solid var(--hover);
  }
}

input,
textarea {
  padding: 20px;
  border: 2px solid #dcddde4d;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

section {
  padding-top: 100px;
  padding-bottom: 60px;
}

.section__title {
  text-transform: uppercase;
  padding-top: 20px;
  position: relative;
  &::after {
    content: "";
    display: block;
    width: 83px;
    height: 4px;
    background: var(--accent);
    position: absolute;
    left: 0;
    top: 0;
  }
}

.section__title.center {
  margin-bottom: 10px;
  text-align: center;
  &::after {
    left: 50%;
    top: 0;
    transform: translate(-50%, -50%);
  }
}

.swiper {
  width: 100%;
  overflow: hidden;
}

.swiper-btn {
  cursor: pointer;
}

.container {
  margin: 0 auto;
  max-width: 1170px;
  padding: 0 15px;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.container {
}
.btn {
  display: flex;
  width: fit-content;
  box-sizing: border-box;
  padding: 20px 30px;
  color: var(--card);
  background: var(--accent);
  border-radius: 5px;
  outline: none;
  border: none;
  transition: 0.3s background-color ease-in-out;
  cursor: pointer;
  &:hover {
    background: var(--hover);
    color: var(--card);
  }
}
