#projects-block {
    .projects {
        &__container {
        }

        &__title {
            margin-bottom: 10px;
        }

        &__subtitle {
            margin-bottom: 60px;
            text-align: center;
            color: var(--text);
        }
        &__swiper {
        }

        &__descr {
            margin-bottom: 30px;
            color: var(--text);
        }

        &__list {
            margin-bottom: 40px;
            &-item {
                display: flex;
                justify-content: space-between;
                align-items: center;
                width: 100%;
                gap: 10px;
            }
            &-name {
                color: var(--text);
            }
            &-line {
                flex-basis: 100%;
                border-top: 1px dotted #797b7d;
                height: 1px;
            }
            &-year {
                color: var(--head);
            }
        }

        &__bottom {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 20px;
        }

        &__btn {
        }

        &__btns {
            display: flex;
            align-items: center;
            gap: 30px;
        }

        &__swiper-button-prev {
        }

        &__swiper-button-next {
        }

        &__img {
            max-height: 380px;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &__content {
            max-width: 470px;
        }
    }
    .swiper-slide {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 30px;
    }
}
