#nums-block {
    background: #f4f5f5;
    .nums {
        &__title {
            margin-bottom: 60px;
        }

        &__list {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 30px;
        }

        &__item {
            flex-basis: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }

        &__icon {
            width: 70px;
            height: 78px;
            img,
            svg {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &__num {
            font-family: var(--fontFamily);
            font-size: 64px;
            font-weight: 700;
            line-height: 78.02px;
            text-align: center;
            text-underline-position: from-font;
            text-decoration-skip-ink: none;
            color: var(--accent);
            margin-bottom: 20px;
        }

        &__name {
            margin-bottom: 10px;
            text-align: center;
        }

        &__descr {
            max-width: 270px;
            text-align: center;
        }
    }
}



@media screen and (max-width: 992px) {
    #nums-block .nums__list {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (max-width: 544px) {
    #nums-block .nums__list {
        display: grid;
        grid-template-columns: 1fr;
    }
}