#about-block {
    padding-bottom: 140px;
    .about {
        &__container {
        }

        &__title {
            text-align: center;
            margin-bottom: 30px;
            &::after {
                left: 50%;
                top: 0;
                transform: translate(-50%, -50%);
            }
        }

        &__descr {
            color: var(--text);
            columns: 2;
            margin-bottom: 30px;
        }

        &__img {
            max-width: 1170px;
            max-height: 271px;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}


@media screen and (max-width: 992px) {
#about-block .about__descr {
    columns: 1;
}
}
