 
    .hero {
    position: relative;
    min-height: 800px;
    padding-top: 147px;
    padding-bottom: 30px;
    &__container {
        max-width: 1530px;
    }
    &__content {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        gap: 20px;
        margin-bottom: 71px;
    }
    &__title-wrapper {
        padding: 30px;
        border: 2px solid;
        border-image-source: linear-gradient(180deg, rgba(255, 255, 255, 0.12) 0%, rgba(255, 255, 255, 0.04) 100%);
        backdrop-filter: blur(60px);
        box-shadow: 0px 4px 40px 0px #ffffff33 inset;
    }

    &__title {
        margin-bottom: 20px;
        max-width: 720px;
        color: var(--card);
    }

    &__btns {
        display: flex;
        gap: 30px;
        align-items: center;
    }

    .swiper__wrapper {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        overflow: hidden;
    }

    .form__wrapper {
        max-width: 490px;
        padding: 30px;
        border: 2px solid;
        border-image-source: linear-gradient(180deg, rgba(255, 255, 255, 0.12) 0%, rgba(255, 255, 255, 0.04) 100%);
        box-shadow: 0px 4px 40px 0px #ffffff33 inset;
        backdrop-filter: blur(60px);
        display: flex;
        flex-direction: column;
    }
    .form__title {
        color: var(--card);
        margin-bottom: 30px;
    }
    label {
        color: var(--card);
        margin-bottom: 10px;
    }
    .input__wrapper {
        margin-bottom: 20px;
        display: flex;
        flex-direction: column;
    }
    input, textarea {
        background: #ffffff1a;
        color: var(--stroke);
        &::placeholder {
            color: var(--stroke);
            border-radius: 5px;
        }
    }
    textarea {
        resize: none;
    }

}
.container {
}
.btn {
}

@media screen and (max-width: 992px) {
    #hero-block .hero__content {
        flex-direction: column;
        align-items: flex-start;
    }
    .hero {
        padding-top: 20px;
    }
}