#text-block {
    .text {
        &__container {
            position: relative;
        }

        &__inner {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 30px;
        }

        &__title {
            margin-bottom: 20px;
        }

        &__descr {
            color: var(--text);
            margin-bottom: 30px;
        }

        &__btn {
        }

        &__img {
            max-width: 570px;
            img {
                width: 100%;
                object-fit: cover;
            }
        }

        &__note {
            position: absolute;
            right: 0;
            bottom: 0;
            box-shadow: 0px 10px 30px 0px #0000001A;
            max-width: 470px;
            display: flex;
            align-items: center;
            padding: 30px;
            background: var(--card);
            gap: 20px;

        }

        &__note-img {
        }

        &__note-text {
        }

        &__note-title {
            text-align: start;
            margin-bottom: 10px;
        }

        &__note-descr {
            color: var(--text);
        }
    }
}


@media screen and (max-width: 1010px) {
#text-block .text__inner {
    grid-template-columns: 1fr;
}
#text-block .text__note {
    position: static;
}
}