#faq-block {
    .faq {
        &__container {
        }

        &__title {
        }

        &__descr {
            text-align: center;
            color: var(--text);
            margin-bottom: 60px;
        }
        &__accordeon {
            display: flex;
            flex-direction: column;
            gap: 20px;
        }
    }

    details[open] {
        summary img {
            transform: rotate(180deg);
        }
        summary {
            border-bottom: none;
        }
    }
    summary {
        padding: 20px;
        border: 2px solid var(--stroke);
        border-radius: 2px;
        display: flex;
        justify-content: space-between;
        &::-webkit-details-marker {
            display: none;
        }
        &::marker {
            display: none;
            content: ""; /* Прощай, стрелочка, теперь ты здесь не нужна! */
        }
        img {
            transition: 0.3s ease-in-out transform;
        }
    }
    details {
        .content {
            padding: 20px;
            border-top: none;
            border: 2px solid var(--stroke);
        }
    }
}
