#consultation-block {
    background: url(./img/consultation-bg.png);
    backdrop-filter: blur(5px);
    background-repeat: no-repeat;
    background-size: cover;
}
#consultation-block {
    .consultation {
        &__container {
        }

        &__title {
            text-align: center;
            color: var(--card);
            margin-bottom: 10px;
            &::after {
                left: 50%;
                top: 0;
                transform: translate(-50%, -50%);
            }
        }

        &__descr {
            margin-bottom: 60px;
            color: var(--card);
            text-align: center;
        }

        &__form {
            padding: 30px;
            background: var(--card);
            display: flex;
            align-items: flex-end;
            gap: 30px;
        }

        &__btn {
            height: fit-content;
            min-width: 209px;
            text-align: center;
        }
    }
    
    label {
    margin-bottom: 10px;
    }
    .input {
        &__wrapper {
            flex-basis: 100%;
            display: flex;
            flex-direction: column;
            gap: 10px;
        }
    }
    input {
        color: var(--text);
        border: 2px solid #DCDDDE;
        &::placeholder {
            color: var(--text);
        }
        &:focus {
            border-color: var(--accent);
        }
    }
    .btn {
    }
    .form {
        &__btn {
        }
    }
}


@media screen and (max-width: 992px) {
#consultation-block .consultation__form {
    flex-direction: column;
}
#consultation-block .input__wrapper {
    width: 100%;
}
#consultation-block .consultation__btn {
    margin: 0px auto;
}
}