#clients-block {
    background: #F4F5F5;
    .clients {
        &__container {
        }

        &__title {
            margin-bottom: 60px;
            align-items: center;
            text-align: center;
            &::after {
                left: 50%;
                top: 0;
                transform: translate(-50%, -50%);
            }
        }
    }
    .container {
    }
    .h1 {
    }
    .section {
        &__title {
        }
    }
    .client {
        &__list {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-items: center;
            gap: 30px;
        }

        &__item {
        }

        &__link {
            max-width: 370px;
            max-height: 230px;
        }
    }
}


@media screen and (max-width: 992px) {
#clients-block .client__list {
    justify-content: center;
}
}