

.footer {
    padding: 30px;
    background: #231f20;
    &__inner {
        display: flex;
        justify-content: space-between;
        gap: 10px;
        align-items: center;
    }
    .privacy-policy {
        color: var(--card);
        opacity: 0.25;
        &:hover {
            opacity: 1;
        }
    }
    .developer {
        color: var(--card);
        opacity: 0.25;
        transition: 0.3s ease-in-out opacity;
        &:hover {
            opacity: 1;
        }
    }
}


@media screen and (max-width: 506px) {
.footer__inner {
    flex-direction: column;
}
}