#contacts-block {
    background: url(./img/contacts-bg.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    .contacts {
        &__container {
            max-width: 1230px;
        }
        &__wrapper {
            display: flex;
            gap: 30px;
        }
        &__title {
            color: var(--card);
            margin-bottom: 60px;
        }

        &__left {
        }

        &__side {
            background: var(--card);
            padding: 30px;
            border-radius: 5px;
            flex-basis: 100%;
        }

        &__side-title {
            margin-bottom: 30px;
        }

        &__side-subtitle {
            margin-bottom: 30px;
            color: var(--text);
        }

        &__name {
            color: var(--accent);
            margin: 0;
            margin-bottom: 10px;
        }
        &__email {
            display: block;
            margin-bottom: 30px;
        }
        &__phone {
            margin-bottom: 30px;
        }

        &__value {
            margin: 0;
            margin-bottom: 30px;
        }

        &__link {
        }

        &__socials {
            display: flex;
            align-items: center;
            gap: 15px;
        }

        &__socials-link {
            width: 40px;
            height: 40px;
            img, svg {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &__right {
        }
    }
    .input__wrapper {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 20px;
        &:last-child {
            margin-bottom: 50px;
        }
    }
    input, textarea {
        color: var(--text);
        background: #F4F5F5;
        resize: none;
    }
}
.container {
}
.h1 {
}
.section {
    &__title {
    }
}
.center {
}
.h3 {
}
.input {
    &__wrapper {
    }
}
.btn {
}
.form {
    &__btn {
    }
}


@media screen and (max-width: 992px) {
#contacts-block .contacts__wrapper {
    flex-direction: column;
}
}