#park-block {
    .park {
        &__container {
        }

        &__title {
            margin-bottom: 60px;
        }
    }
    .container {
    }
    .h1 {
    }
    .section {
        &__title {
        }
    }
    .center {
    }
    .tab {
        &__content {
            max-width: 470px;
            
        }

        &__title {
            margin-bottom: 10px;
        }

        &__descr {
            margin-bottom: 20px;
            color: var(--text);
        }

        &__img {
            max-height: 430px;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &__list {
            margin-bottom: 40px;
            &-item {
                display: flex;
                justify-content: space-between;
                align-items: center;
                width: 100%;
                gap: 10px;
            }
            &-name {
                color: var(--text);
            }
            &-line {
                flex-basis: 100%;
                border-top: 1px dotted #797B7D;
                height: 1px;
            }
            &-year {
                color: var(--head);
            }
        }
    }
    .tab-content {
    }

    .tab {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    .tab > input[type="radio"] {
        display: none;
    }

    .tab-content {
        display: none;
        width: 100%;
        margin-top: 1rem;
    }

    #tab-btn-1:checked ~ #content-1,
    #tab-btn-2:checked ~ #content-2,
    #tab-btn-3:checked ~ #content-3,
    #tab-btn-4:checked ~ #content-4,
    #tab-btn-5:checked ~ #content-5
     {
        display: flex;
    }

    .tab > label {
        display: block;
        padding: 0.5rem 1rem;
        cursor: pointer;
        transition:
            color 0.15s ease-in-out,
            background-color 0.15s ease-in-out,
            border-color 0.15s ease-in-out;
        text-decoration: none;
        color: var(--text);
        border: 0;
        border-radius: 0.375rem;
        background: 0 0;
        border: 1px solid var(--stroke);
        margin-right: 10px;
        &:last-child {
            margin-right: 0;
        }

        font-family: var(--fontFamily);
        font-size: 18px;
        font-weight: 400;
        line-height: 24.3px;
        text-align: center;
        text-underline-position: from-font;
        text-decoration-skip-ink: none;
    }

    .tab > input[type="radio"]:checked + label {
        cursor: default;
        color: #fff;
        background-color: var(--accent);
        border: none;
    }
}


@media screen and (max-width: 1100px) {
#park-block .tab-content {
    flex-direction: column;
}
#park-block .tab {
    gap: 10px;
}
}
