.header {
    .container {
        max-width: 1530px;
    }
    &__top {
        padding: 13px 0;
        &-inner {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }

    &__logo {
    }

    &__top-wrapper {
        display: flex;
        align-items: center;
        gap: 30px;
    }

    &__contact {
        display: flex;
        align-items: center;
        gap: 10px;
        font-family: var(--fontFamily);
        font-size: 18px;
        font-weight: 700;
        line-height: 24.3px;
        text-align: left;
        text-underline-position: from-font;
        text-decoration-skip-ink: none;
    }

    &__phones {
        display: flex;
        flex-direction: column;
        gap: 5px;
        a {
            display: block;
        }
    }

    &__btn {
        text-transform: uppercase;
    }

    &__bottom {
        width: 100%;
        background: var(--head);
    }

    &__burger {
      max-width: 30px;
      max-height: 30px;
      display: none;
      cursor: pointer;
      svg {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &__mobile {
      
      padding: 30px;
      position: absolute;
      max-width: 320px;
      top: 0;
      z-index: 3;
      left: 0;
      left: -300px;
      height: 100%;
      display: flex;
      flex-direction: column;
      background: var(--head);
      transition: all 0.3s ease-in-out;
      &.active {
        left: 0;
      }
      .header__nav{
        flex-direction: column;
      }
      .header__phones a {
        color: var(--card);
      }
    }

    &__nav {
        padding: 15px 0;
        display: flex;
        list-style: none;
        gap: 30px;
        justify-content: center;
    }

    &__item {
    }

    &__link {
        font-family: var(--fontFamily);
        font-size: 16px;
        font-weight: 700;
        line-height: 21.6px;
        text-align: left;
        text-underline-position: from-font;
        text-decoration-skip-ink: none;
        text-transform: uppercase;
        color: var(--card);
    }
}



.phone {
  background-color: #36383F;
  border-radius: 40px;
  width: 300px;
  height: 600px;
  Display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 30px 50px 100px #85888C;
}
.content {
  border: 1px solid #36383F;
  border-radius: 20px;
  width: 94%;
  height: 91%;
  background-color: #F5F6FA;
  overflow: hidden;
}
nav {
  background-color: #1E1E23;
  height: 65px;
}


#menuToggle {
  display: flex;
  flex-direction: column;
  position: relative;
  top: 25px;
  left: 25px;
  z-index: 1;
  -webkit-user-select: none;
  user-select: none;
}

#menuToggle input
{
  display: flex;
  width: 40px;
  height: 32px;
  position: absolute;
  cursor: pointer;
  opacity: 0;
  z-index: 2;
}

#menuToggle span
{
  display: flex;
  width: 29px;
  height: 2px;
  margin-bottom: 5px;
  position: relative;
  background: #ffffff;
  border-radius: 3px;
  z-index: 1;
  transform-origin: 5px 0px;
  transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
              background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
              opacity 0.55s ease;
}

#menuToggle span:first-child
{
  transform-origin: 0% 0%;
}

#menuToggle span:nth-last-child(2)
{
  transform-origin: 0% 100%;
}

#menuToggle input:checked ~ span
{
  opacity: 1;
  transform: rotate(45deg) translate(-3px, -1px);
  background: #36383F;
}
#menuToggle input:checked ~ span:nth-last-child(3)
{
  opacity: 0;
  transform: rotate(0deg) scale(0.2, 0.2);
}

#menuToggle input:checked ~ span:nth-last-child(2)
{
  transform: rotate(-45deg) translate(0, -1px);
}

#menu
{
  position: absolute;
  width: 180px;
  height: 400px;
  box-shadow: 0 0 10px #85888C;
  margin: -50px 0 0 -50px;
  padding: 50px;
  padding-top: 125px;
  background-color: #F5F6FA;
  -webkit-font-smoothing: antialiased;
  transform-origin: 0% 0%;
  transform: translate(-100%, 0);
  transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
}

#menu li
{
  padding: 10px 0;
  transition-delay: 2s;
}

#menuToggle input:checked ~ ul
{
  transform: none;
}
@media screen and (max-width: 1450px) {
  .header__top .header__contact {
    display: none;
  }
  .header__bottom {
    display: none;
  }
  .header__burger {
    display: block;
  }
}

@media screen and (max-width: 574px) {
  .header__btn {
    display: none;
  }
}